import utils from './utils';
import { management } from '../index';

// –°–æ–∑–¥–∞–µ—Ç —Ñ–æ—Ä–º—É —Å —Ä–∞–∑–ª–∏—á–Ω—ã–º –Ω–∞–ø–æ–ª–Ω–µ–Ω–∏–µ–º –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ –∏–º–µ–Ω–∏, –ø–µ—Ä–µ–¥–∞–µ—Ç –Ω–∞ —Ä–µ–Ω–¥–µ—Ä;

const createForm = tagName => {
    let form;
    if (tagName === '–ó–∞–≥–æ–ª–æ–≤–æ–∫') {
        form = utils.form(` ${utils.input('content', '–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç –∑–∞–≥–æ–ª–æ–≤–∫–∞')}
                            ${utils.select('tag', ['h1', 'h2', 'h3', 'h4', 'h5', 'h6'])}       
                            ${utils.textarea('styles', '–í–≤–µ–¥–∏—Ç–µ –∂–µ–ª–∞–µ–º—ã–µ —Å—Ç–∏–ª–∏ –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫–∞')}
                            ${utils.submitBtn()}`);
    } else if (tagName === '–û–±—ã—á–Ω—ã–π —Ç–µ–∫—Å—Ç') {
        form = utils.form(` ${utils.input('content', '–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç –∞–±–∑–∞—Ü–∞')}                          
                            ${utils.textarea('styles', '–í–≤–µ–¥–∏—Ç–µ –∂–µ–ª–∞–µ–º—ã–µ —Å—Ç–∏–ª–∏ –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫–∞')}
                            ${utils.submitBtn()}`);
    } else if (tagName === '–ö–æ–ª–æ–Ω–∫–∏ —Å —Ç–µ–∫—Å—Ç–æ–º') {
        form = utils.form(` ${utils.textarea('content', '–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç –∞–±–∑–∞—Ü–µ–≤ –¥–ª—è –∫–∞–∂–¥–æ–≥–æ —Å—Ç–æ–ª–±—Ü–∞ —á–µ—Ä–µ–∑ ;')}                          
                            ${utils.textarea('styles', '–í–≤–µ–¥–∏—Ç–µ –∂–µ–ª–∞–µ–º—ã–µ —Å—Ç–∏–ª–∏ –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫–∞')}
                            ${utils.submitBtn()}`);
    } else if (tagName === '–ò–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ') {
        form = utils.form(` ${utils.input('content', '–ü—É—Ç—å –¥–æ –∫–∞—Ä—Ç–∏–Ω–∫–∏')}                          
                            ${utils.textarea('styles', '–í–≤–µ–¥–∏—Ç–µ –∂–µ–ª–∞–µ–º—ã–µ —Å—Ç–∏–ª–∏ –¥–ª—è –∫–∞—Ä—Ç–∏–Ω–∫–∏')}
                            ${utils.submitBtn()}`);
    }
    return form;
}

// –ò–≤–µ–Ω—Ç –æ—Ç–ø—Ä–∞–≤–∫–∏ –¥–∞–Ω–Ω—ã—Ö –∏–∑ —Ñ–æ—Ä–º—ã, –ø–µ—Ä–µ–¥–∞–µ—Ç –¥–∞–Ω–Ω—ã–µ —Ñ—É–Ω–∫—Ü–∏–∏ –∫–æ—Ç–æ—Ä–∞—è —Å–æ–∑–¥–∞–µ—Ç –≤–µ—Ä–Ω—É—é —Å—Ç—Ä—É–∫—Ç—É—Ä—É –¥–∞–Ω–Ω—ã—Ö;

const onSubmitForm = e => {
    e.preventDefault();

    const $mainSelect = document.querySelector('.main-select');
    const dataClass = $mainSelect.options[$mainSelect.selectedIndex].value;
    const formChildren = e.target.children;
    let mainData = {};

    for (let elem of formChildren) {
        if(elem.value) {
            mainData[elem.name] = elem.value;
        }
    }
    management.addNewData(dataClass, mainData);
}

export {
    createForm,
    onSubmitForm
};